

















import { Vue, Component, Prop } from "vue-property-decorator";
import VTooltip from "v-tooltip";

Vue.use(VTooltip);

@Component
export default class Tootip extends Vue {
  @Prop() content?: string;
  @Prop({ default: "default" }) position?: string;
  positions: object = {
    default: "",
    corner: "tooltip-corner",
  };
}
