





































































































































import { Component, Vue, Watch } from "vue-property-decorator";
import NavTabs from "@/components/NavTabs.vue";
import NavInvest from "@/components/NavInvest.vue";
import Loader from "@/components/Loader.vue";
import Form, { FormField, FormIcon, FormInfo } from "@/components/Form";
import SubmitBtn from "@/components/SubmitBtn.vue";

import { OpKind } from "@taquito/taquito";
import BigNumber from "bignumber.js";
import { loadTokens, getAccount, useWallet } from "@/store";
import {
  QSAsset,
  isAddressValid,
  isKTAddress,
  toValidAmount,
  getBalance,
  getNewTokenData,
  getDexStorage,
  getStoragePure,
  getContract,
  getNetwork,
  estimateInTokens,
  estimateInTezos,
  tzToMutez,
  mutezToTz,
  clearMem,
  approveToken,
  QSTokenType,
  deapproveFA2,
  isUnsafeAllowanceChangeError,
  confirmOperation,
} from "@/core";
import { XTZ_TOKEN } from "@/core/defaults";
import { notifyConfirm, notifyError } from "../toast";

type PoolMeta = {
  tezFull: string;
  tokenFull: string;
  myShare: string;
  myTokens: string;
};

@Component({
  components: {
    NavTabs,
    NavInvest,
    Form,
    FormField,
    FormIcon,
    FormInfo,
    SubmitBtn,
    Loader,
  },
})
export default class AddToken extends Vue {
  tokenAddress = "";

  tezToken: QSAsset | null = XTZ_TOKEN;
  tezAmount = "";
  tezBalance: string | null = null;
  tezLoading = false;

  tokenAmount = "";
  tokenBalance: string | null = null;
  tokenDecimals = 0;
  tokenLoading = false;

  processing = false;
  addTokenStatus = this.defaultAddTokenStatus;
  tokenDataLoaded = false;

  tokenType = QSTokenType.FA1_2;
  tokenId = 0;

  get defaultAddTokenStatus() {
    return "Add new token";
  }

  get account() {
    return getAccount();
  }

  get noDecimalsToken() {
    // return this.tokenDataLoaded
    //   ? this.tokenDecimals === 0 ||
    //     new BigNumber(this.tezAmount)
    //       .times(10 ** 6)
    //       .div(
    //         new BigNumber(this.tokenAmount).times(10 ** this.tokenDecimals)
    //       ).isGreaterThan(15000)
    //   : false;
    return false;
  }

  get valid() {
    return (
      this.tezToken &&
      isKTAddress(this.tokenAddress) &&
      this.tokenDataLoaded &&
      !this.noDecimalsToken &&
      [this.tezAmount, this.tokenAmount].every((a) => a && +a > 0)
      // new BigNumber(this.tokenAmount).toFormat(this.tokenDecimals, {
      //   decimalSeparator: ".",
      // }) === this.tokenAmount
    );
  }

  get exchangeRate() {
    if (!this.valid) {
      return null;
    }

    const price = new BigNumber(this.tezAmount)
      .div(this.tokenAmount)
      .toFormat(6);

    return `1 Token = ${price} XTZ`;
  }

  setTokenType(tokenType: QSTokenType) {
    this.tokenType = tokenType;
  }

  created() {
    this.loadTezBalance();
  }

  @Watch("tezToken")
  onTezTokenChange() {
    this.loadTezBalance();
  }

  @Watch("tokenType")
  onTokenTypeChange() {
    this.loadTokenData();
  }

  @Watch("tokenAddress")
  onTokenAddressChange() {
    this.loadTokenData();
  }

  @Watch("tokenId")
  onTokenIdChange() {
    this.loadTokenData();
  }

  @Watch("account")
  onAccountChange() {
    this.loadTezBalance();
    this.loadTokenData();
  }

  async loadTezBalance() {
    this.tezBalance = null;
    try {
      if (this.tezToken && this.account.pkh) {
        const balance = await getBalance(this.account.pkh, this.tezToken);
        this.tezBalance = balance.toFixed();
      }
    } catch (err) {
      if (process.env.NODE_ENV === "development") {
        console.error(err);
      }
    }
  }

  async loadTokenData() {
    this.tokenBalance = null;
    this.tokenDataLoaded = false;
    if (isKTAddress(this.tokenAddress) && this.account.pkh) {
      try {
        const { bal, decimals } = await getNewTokenData(
          this.account.pkh,
          this.tokenType,
          this.tokenAddress,
          this.tokenId ? +this.tokenId : 0
        );
        this.tokenBalance = bal.div(new BigNumber(10).pow(decimals)).toFixed();
        this.tokenDecimals = decimals;
      } catch (err) {
        if (process.env.NODE_ENV === "development") {
          console.error(err);
        }
        this.tokenBalance = "?";
        this.tokenDecimals = 0;
      }

      this.tokenDataLoaded = true;
    }
  }

  handleTezAmountChange(amount: string) {
    this.tezAmount = amount;
  }

  handleTokenAmountChange(amount: string) {
    this.tokenAmount = amount;
  }

  async addToken() {
    if (this.processing) return;
    this.processing = true;
    try {
      const tezos = await useWallet();
      const me = await tezos.wallet.pkh();

      const tezTk = this.tezToken!;
      const tezAmount = new BigNumber(this.tezAmount);
      const tokenAmount = new BigNumber(this.tokenAmount)
        .multipliedBy(new BigNumber(10).pow(this.tokenDecimals))
        .integerValue();
      const tokenId = this.tokenId ? +this.tokenId : 0;

      const tokenData = await getNewTokenData(
        me,
        this.tokenType,
        this.tokenAddress,
        tokenId
      );

      const toCheck = [
        {
          promise: getBalance(me, tezTk),
          amount: tezAmount,
        },
        {
          promise: Promise.resolve(tokenData).then(({ bal }) => bal),
          amount: tokenAmount,
        },
      ];
      for (const { promise, amount } of toCheck) {
        let bal: BigNumber | undefined;
        try {
          bal = await promise;
        } catch (_err) {}
        if (bal && bal.isLessThan(amount)) {
          throw new Error("Not Enough Funds");
        }
      }

      const { fa1_2FactoryContract, fa2FactoryContract } = getNetwork();
      if (
        (this.tokenType === "FA1.2" && !fa1_2FactoryContract) ||
        (this.tokenType === "FA2" && !fa2FactoryContract)
      ) {
        throw new Error("Factory contract for network not found");
      }

      const factoryContractAddres =
        this.tokenType === "FA1.2"
          ? fa1_2FactoryContract!
          : fa2FactoryContract!;

      const [facContract, tokenContract] = await Promise.all([
        tezos.wallet.at(factoryContractAddres),
        tezos.wallet.at(this.tokenAddress),
      ]);

      const tokenAmountNat = tokenAmount
        .integerValue(BigNumber.ROUND_DOWN)
        .toFixed();

      const facStorage: any = await facContract.storage();
      const dexAddress = await facStorage.token_to_exchange.get(
        this.tokenType === "FA1.2" ? this.tokenAddress : [this.tokenAddress, tokenId]
      );

      let withAllowanceReset = false;
      try {
        await tezos.estimate.batch([
          {
            kind: OpKind.TRANSACTION,
            ...approveToken(
              {
                tokenType: this.tokenType,
                fa2TokenId: tokenId
              },
              tokenContract,
              me,
              dexAddress || factoryContractAddres,
              tokenAmountNat
            ).toTransferParams()
          },
        ]);
      } catch (err) {
        if (isUnsafeAllowanceChangeError(err)) {
          withAllowanceReset = true;
        } else {
          console.error(err);
        }
      }

      let batch = tezos.wallet.batch([]);

      if (withAllowanceReset) {
        batch = batch.withTransfer(
          approveToken(
            {
              tokenType: this.tokenType,
              fa2TokenId: tokenId
            },
            tokenContract,
            me,
            dexAddress || factoryContractAddres,
            0
          ).toTransferParams()
        );
      }

      batch = batch
        .withTransfer(
          approveToken(
            {
              tokenType: this.tokenType,
              fa2TokenId: tokenId
            },
            tokenContract,
            me,
            dexAddress || factoryContractAddres,
            tokenAmountNat
          ).toTransferParams()
        );

      batch = dexAddress
        ? batch
        .withTransfer(
          (await tezos.wallet.at(dexAddress)).methods
            .initializeExchange(
              tokenAmountNat
            )
            .toTransferParams({ amount: tezAmount.toFixed() as any })
        )
        : batch
          .withTransfer(
          facContract.methods
            .launchExchange(
              ...(this.tokenType === "FA1.2"
                ? [this.tokenAddress]
                : [this.tokenAddress, tokenId]),
              tokenAmountNat
            )
            .toTransferParams({ amount: tezAmount.toFixed() as any })
        );

      deapproveFA2(
        batch,
        {
          tokenType: this.tokenType,
          fa2TokenId: tokenId
        },
        tokenContract,
        me,
        dexAddress || factoryContractAddres,
      );

      const operation = await batch.send();

      notifyConfirm(
        confirmOperation(tezos, operation.opHash)
          .finally(() => this.refresh())
      );
    } catch (err: any) {
      console.error(err);
      notifyError(err);
      const msg = err.message;
      this.addTokenStatus =
        msg && msg.length < 30
          ? msg.startsWith("Dex/")
            ? msg.replace("Dex/", "")
            : msg
          : "Something went wrong";
    }
    this.processing = false;

    await new Promise((res) => setTimeout(res, 5000));
    this.addTokenStatus = this.defaultAddTokenStatus;
  }

  refresh() {
    clearMem();
    this.loadTezBalance();
    this.loadTokenData();
    loadTokens();
  }
}
